import {useNavigate} from 'react-router-dom';
import {useEffect, useState} from "react";
import PieChart from "../../components/PieChart";
import LineChart from "../../components/LineChart";

import {toast} from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import {userApi} from "../../services";
import moment from "moment/moment";

toast.configure();

const Home = ({isLoggedIn}) => {
    const navigate = useNavigate();

    const [data, setData] = useState([]);
    const [lineChartData, setLineChartData] = useState([]);
    const [mounted, setMounted] = useState(false);

    const reducer = (previousValue, currentValue) => previousValue + currentValue;

    useEffect(()=>{
        if (!isLoggedIn){
            navigate('/login')
        }
    },[isLoggedIn]);

    useEffect(()=>{
        fetchUsers();
        return () => {
            setMounted(true);
        }
    },[])

    const fetchUsers = async () => {
        try {
            const response = await userApi.getAllUsers();
            setLineChartData([...response].map(el=>{
                return{
                    name:el.login,
                    data:[...el.money].map(pr=>{
                        return {
                            y:pr.count,
                            date: moment(pr.time).format('DD-MM-YYYY')
                        }
                    })
                }
            }))
            setData([...response].map(el=>{
                const arr = el.money.map(el=>el.count);
                const sum = arr.length > 0 ? arr.reduce(reducer): 0;
                return {
                   name:el.login,
                    y: sum
                }
            }))
        }catch (e) {
            toast.error(e.message, {
                position:'top-center'
            })
        }
    }

    return(
        <div>
            {
                data.length > 0 && <PieChart data={data}/>
            }
            {
                data.length > 0 && <LineChart data={lineChartData}/>
            }
        </div>
    )
}

export default Home;